html,
body {
  box-sizing: border-box;
  font-size: 16px;
  font-family: 'Lato', sans-serif;
  color: #fff; }

*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

.container {
  width: 100%;
  max-width: 1200px;
  margin-right: auto;
  margin-left: auto;
  padding: 0 0.5rem; }

#header {
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../assets/img/elcapitan.jpg");
  background-position: center;
  background-size: cover;
  overflow: hidden; }
  @media (orientation: portrait) {
    #header {
      height: 100px;
      border-bottom: 2px solid #F9C802; } }
  #header--small {
    height: 10vh;
    border-bottom: 2px solid #F9C802; }

.nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0.5rem; }
  @media (min-width: 900px) {
    .nav {
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      padding-top: 1rem; } }
  .nav__logo {
    font-size: 2rem;
    margin-bottom: .5rem;
    font-family: 'Sacramento', cursive; }
    @media (min-width: 900px) {
      .nav__logo {
        font-size: 3rem;
        margin-bottom: 0; } }
  .nav__items {
    display: flex; }
  .nav__link {
    display: inline-block;
    text-decoration: none;
    text-transform: uppercase;
    color: #fff;
    font-size: 1rem;
    margin: 0 0.5rem;
    padding-bottom: 0.5rem;
    font-weight: 300;
    border-bottom: 2px solid transparent;
    transition: border-color .2s linear; }
    .nav__link:hover {
      border-color: #fff; }
    .nav__link.active {
      border-color: #F9C802; }
    @media (min-width: 900px) {
      .nav__link {
        font-size: 1.2rem; } }

ul {
  list-style: none;
  padding: 0;
  margin: 0; }

a {
  text-decoration: none;
  color: #fff;
  display: inline-block; }

.btn {
  background-color: #fff;
  color: #000;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  letter-spacing: 1px;
  transition: all .2s linear; }
  .btn:hover {
    background-color: #F9C802; }

.section-header {
  text-align: center;
  color: #333333;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  margin-top: 2rem;
  margin-bottom: 3em; }
  .section-header__header {
    font-size: 1.8rem;
    text-transform: uppercase;
    font-weight: 300;
    word-spacing: 4px;
    letter-spacing: 1px; }
  .section-header__border {
    background-color: #F9C802;
    height: 2px;
    width: 50px;
    margin-top: 1rem;
    margin-bottom: 1rem;
    margin-left: auto;
    margin-right: auto; }
  .section-header__paragraph {
    font-size: 1.2rem;
    line-height: 1.6; }

#footer {
  background-color: #4d4d4d;
  color: #fff;
  padding-top: 2rem;
  padding-bottom: 2rem; }
  #footer .footer--container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center; }
    @media (orientation: landscape) {
      #footer .footer--container {
        flex-direction: row; } }
    #footer .footer--container .copyright {
      font-size: 1rem;
      margin-bottom: 1.4rem;
      text-align: center;
      line-height: 1.4; }
      @media (orientation: landscape) {
        #footer .footer--container .copyright {
          margin-bottom: 0; } }
    #footer .footer--container .socials {
      display: flex;
      cursor: pointer; }
      #footer .footer--container .socials__item {
        border: 2px solid #fff;
        padding: 0.5rem;
        font-size: 1.2rem;
        border-radius: 50%;
        margin-right: 1rem; }
        #footer .footer--container .socials__item:hover {
          background-color: #F9C802; }
        #footer .footer--container .socials__item:last-child {
          margin-right: 0; }

#header .content {
  width: 100%;
  height: 100%;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center; }
  @media (min-width: 900px) {
    #header .content {
      justify-content: space-evenly; } }
  #header .content__header {
    font-size: 1.4rem;
    line-height: 1.6; }
    @media (min-width: 900px) {
      #header .content__header {
        font-size: 3rem;
        letter-spacing: 1px;
        line-height: 1.8; } }
  #header .content__paragraph {
    font-size: 1rem;
    line-height: 1.4;
    margin-bottom: 2rem; }
    @media (min-width: 900px) {
      #header .content__paragraph {
        font-size: 1.6rem;
        letter-spacing: 1px;
        line-height: 1.6; } }
  @media (orientation: portrait) {
    #header .content {
      display: none; } }

#home-a {
  color: #000;
  background-color: #fff; }
  #home-a .specials {
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;
    text-align: center;
    margin-bottom: 2rem; }
    @media (orientation: landscape) {
      #home-a .specials {
        grid-template-columns: repeat(4, 1fr); } }
    #home-a .specials .item {
      padding-top: 0.5rem;
      padding-bottom: 0.5rem; }
      #home-a .specials .item__icon {
        font-size: 2.5rem;
        color: #F9C802;
        margin-bottom: 1rem; }
      #home-a .specials .item__header {
        color: black;
        text-transform: uppercase;
        font-size: 1.2rem;
        margin-bottom: 1rem; }
      #home-a .specials .item__paragraph {
        color: #333333;
        font-size: 1rem;
        line-height: 1.4; }
      @media (min-width: 900px) {
        #home-a .specials .item__header {
          font-size: 1.4rem; }
        #home-a .specials .item__paragraph {
          font-size: 1.2rem; } }
  #home-a .statistics {
    background-color: yellow;
    display: grid;
    grid-template-columns: 1fr;
    text-align: center; }
    @media (orientation: landscape) {
      #home-a .statistics {
        grid-template-columns: repeat(4, 1fr); } }
    #home-a .statistics .stat-item {
      background-color: #b4b4b4;
      height: 220px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center; }
      #home-a .statistics .stat-item:nth-child(odd) {
        background-color: #909090; }
      #home-a .statistics .stat-item__icon {
        color: #333333;
        font-size: 2rem;
        margin-bottom: 1rem; }
      #home-a .statistics .stat-item__header {
        color: #333333;
        font-size: 1.4rem;
        font-weight: 300;
        letter-spacing: 1px;
        margin-bottom: 1rem; }
      #home-a .statistics .stat-item__numbers {
        font-size: 1.6rem;
        font-weight: 700; }
      @media (min-width: 900px) {
        #home-a .statistics .stat-item {
          height: 280px; }
          #home-a .statistics .stat-item__icon {
            font-size: 3rem; } }

#home-b {
  color: #000;
  text-align: center;
  margin-top: 2rem;
  margin-bottom: 2rem; }
  #home-b .section-process-container {
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem; }
    @media (orientation: landscape) {
      #home-b .section-process-container {
        grid-template-columns: repeat(4, 1fr); } }
    #home-b .section-process-container .single-process {
      border-bottom: 1px dashed #000;
      padding-bottom: 1rem;
      margin-bottom: 1rem; }
      @media (orientation: landscape) {
        #home-b .section-process-container .single-process {
          border-bottom: none;
          padding-bottom: 0; } }
      #home-b .section-process-container .single-process__icon {
        position: relative;
        display: inline-block;
        color: #fff;
        background-color: #4d4d4d;
        border-radius: 50%;
        cursor: pointer;
        transition: all 0.3s linear; }
        #home-b .section-process-container .single-process__icon:hover {
          background-color: #F9C802;
          transform: scale(1.2); }
        #home-b .section-process-container .single-process__icon:hover p {
          background-color: #4d4d4d; }
        #home-b .section-process-container .single-process__icon i {
          display: block;
          width: 100px;
          height: 100px;
          line-height: 100px;
          font-size: 3rem; }
        #home-b .section-process-container .single-process__icon p {
          position: absolute;
          top: 0;
          right: -10px;
          background-color: #F9C802;
          font-size: 1.2rem;
          padding: 0.5rem;
          border-radius: 50%;
          width: 40px;
          height: 40px;
          transition: all 0.3s linear; }
      #home-b .section-process-container .single-process__header {
        font-size: 1.2rem;
        text-transform: uppercase;
        margin-top: 1rem;
        margin-bottom: 1rem; }
      #home-b .section-process-container .single-process__paragraph {
        font-size: 1rem;
        line-height: 1.6;
        color: #4d4d4d; }
